import { Component, OnInit, Inject, Optional } from '@angular/core';
import {Router} from '@angular/router';

import { MatDialogRef } from "@angular/material/dialog";
import { MAT_DIALOG_DATA } from '@angular/material/dialog';

import { SharedService } from '../../services/shared.service';
import { ApartmentService } from '../../../api/services/apartment.service';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-apartment-dialog',
  templateUrl: './apartment-dialog.component.html',
  styleUrls: ['./apartment-dialog.component.scss']
})
export class ApartmentDialogComponent implements OnInit {
  
  apartmentList:any;

  isDataLoaded:boolean = false;

  constructor(
    private router: Router,
    @Optional() @Inject(MAT_DIALOG_DATA) public index: any,
  	private dialogRef: MatDialogRef<ApartmentDialogComponent>,
  	private apartmentService:ApartmentService,
  	private cookieService:CookieService,
    private sharedService:SharedService
  ) { }

  dialogClose() {
    this.dialogRef.close();
  }

  deleteItem() {
    this.sharedService.setUnitListDeleteIndex(this.index);
    this.dialogRef.close();
  }

  selectApartment(id){
    /*if(this.cookieService.get('apartmentId') == ""){
      this.cookieService.set('apartmentId', id);
    }
    else {
      this.cookieService.delete('apartmentId');
      this.cookieService.set('apartmentId', id);
    }*/
    
    this.cookieService.delete('apartmentId');
    this.cookieService.set('apartmentId', id);

  	this.sharedService.setApartmentSelected(true);
    this.dialogRef.close();
    
  }

  ngOnInit() {

  	this.apartmentService.getAllApartmentsByUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {
      
      this.apartmentList = res;
      this.isDataLoaded = true;

    },
    error => {
      console.log(error);
    }
    );

  }

}
