import { Component, OnInit, Optional, Inject, Injector } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { SharedService } from 'src/app/shared/services/shared.service';
import { ModalService } from 'src/app/shared/services/modal.service';

@Component({
  selector: 'app-facility-reject-confirmation-modal',
  templateUrl: './facility-reject-confirmation-modal.component.html',
  styleUrls: ['./facility-reject-confirmation-modal.component.scss']
})
export class FacilityRejectConfirmationModalComponent implements OnInit {
  modalService:any;
  constructor(
    @Optional() @Inject(MAT_DIALOG_DATA) public facility: any,
  	private confirmDialogRef: MatDialogRef<FacilityRejectConfirmationModalComponent>,
    private sharedService:SharedService,
    private injector: Injector,
  ) { 
    this.modalService = this.injector.get(ModalService);
  }

  dialogClose() {
    this.confirmDialogRef.close();
  }

  rejected() {
    this.modalService.showRejctDetailsModal(this.facility);
    this.confirmDialogRef.close();
  }

  ngOnInit() {
  }

}
