import { Component, OnInit, OnDestroy, Inject, Renderer2 } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';
import { DOCUMENT } from '@angular/common';
import { NgwWowService } from 'ngx-wow';
import { Subscription }   from 'rxjs';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss']
})
export class AppComponent implements OnInit, OnDestroy {
  title = 'app-ng8';

  private wowSubscription: Subscription;
 
  constructor(private router: Router, private wowService: NgwWowService, @Inject(DOCUMENT) document, 
    r: Renderer2 ){

    let bodyClassList:string = 'home';

    router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        this.wowService.init(); 


        let path = this.router.url;
        let name = path.split('/');
        r.removeClass(document.body,bodyClassList);
        r.addClass(document.body, name[1]);
        bodyClassList = document.body.classList.value;

      }
    });

  
  }


  ngOnInit() {
    // you can subscribe to WOW observable to react when an element is revealed
    this.wowSubscription = this.wowService.itemRevealed$.subscribe(
      (item:HTMLElement) => {
        // do whatever you want with revealed element
      });
  }
 
  ngOnDestroy() {
    // unsubscribe (if necessary) to WOW observable to prevent memory leaks
    this.wowSubscription.unsubscribe();
  }

}
