import { Component, OnInit, Optional, Inject, ChangeDetectorRef } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { SharedService } from 'src/app/shared/services/shared.service';
import * as moment from 'moment';
import { FacilityService } from 'src/app/api/services';
import { Router } from '@angular/router';
import { CookieService } from 'ngx-cookie-service';

@Component({
  selector: 'app-facility-reject-action',
  templateUrl: './facility-reject-action.component.html',
  styleUrls: ['./facility-reject-action.component.scss']
})
export class FacilityRejectActionComponent implements OnInit {

  isBookingSubmitted:boolean = true;
  isError:boolean = true;
  alertMessage:string = "";

  constructor(@Optional() @Inject(MAT_DIALOG_DATA) public facility: any,
  private dialogRef: MatDialogRef<FacilityRejectActionComponent>,
  private sharedService:SharedService,private cookieService:CookieService, 
  private facilityService:FacilityService, private router: Router,private cd:ChangeDetectorRef) { }
  ngOnInit(): void {
    this.facility.comment = ""
  }
  dialogClose() {
  	this.dialogRef.close();
  }
  isAdmin(){
    return ( this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') ? true : false;
  }
  textChanged(){
    if(this.facility.comment !== "")
      this.isBookingSubmitted = false;
    else
      this.isBookingSubmitted = true;
    this.cd.markForCheck();
  }
  reject(){
    if(this.isBookingSubmitted === false)
     {
    let details = {
      "apartmentFacilityBookingId": this.facility.apartmentFacilityBookingId,
      "apartmentId": this.facility.apartmentId,
      "apartmentFacilityId": parseInt(this.facility.apartmentFacilityId),
      "bookedOn": this.facility.bookedOn, 
      "bookedForDate": this.facility.bookedForDate,
      "slotId": parseInt(this.facility.slotId),
      "facilityBookingStatusId": 385,
      "isCancelled": this.facility.isCancelled,
      "cancelledBy": this.facility.cancelledBy,
      "cancelledOn": this.facility.cancelledOn,
      "cancelledAmount": this.facility.cancelledAmount,
      "neededUserId": parseInt(this.facility.neededUserId),
      "amount": this.facility.amount,
      "comment": this.facility.comment,
      "isActive": this.facility.isActive,
      "insertedBy": this.facility.insertedBy,
      "insertedOn":this.facility.insertedOn,
      "updatedBy": parseInt(this.cookieService.get('userId')),
      "updatedOn":  moment().format()

  }


  this.facilityService.updateApartmentFacilityBooking(details).subscribe((res:any) => {

    if(res.message){

     
      this.sharedService.setAlertMessage("Booking Rejected successfully");
      this.router.navigate(['ams/facility/all-bookings']);
    }
    else {
      this.isBookingSubmitted = false;
      this.isError = true;
      this.alertMessage = res.errorMessage;
    }

  },
  error => {

    this.isBookingSubmitted = false;
    this.isError = true;
    this.alertMessage = "Some error occured";

  });
  this.dialogRef.close();
  }
}
}
