import { Component, OnInit, ChangeDetectorRef, Optional, Inject } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService, EmailSendService, ApartmentService, LookupService } from 'src/app/api/services';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import moment from 'moment';

@Component({
  selector: 'app-signup-edit-modal',
  templateUrl: './signup-edit-modal.component.html',
  styleUrls: ['./signup-edit-modal.component.scss']
})
export class SignupEditModalComponent implements OnInit {
  blocksData:any;
  unitData:any;
  userRolesData:any;

  userType:string = null;
  genderType:string = null;

  apartmentBlockId:string = "";
  apartmentBlockUnitId:string = "";
  userRoleId:string = "";

  isEditUser:boolean = false;

  isBlockSelected:boolean = false;

  isUserSubmitted:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";
  userenteredUserType:string="";
  userenteredBlockName:string="";
  userenteredUnitName:string="";

  user:any;
  constructor( private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private emailSendService:EmailSendService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private cd:ChangeDetectorRef,
    @Optional() @Inject(MAT_DIALOG_DATA) public signupData: any,
    private dialogRef: MatDialogRef<SignupEditModalComponent>,) { }

  ngOnInit() {
    console.log(this.signupData);
    this.user = this.signupData;
    this.isEditUser = this.user.mode;
    this.user.isEmailNotify = true;

    //get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.blocksData = res;
    });
    this.genderType = this.user.genderName;
    var nots = JSON.parse(this.user.notes);
    if(nots.UserType == 1){
      this.userenteredUserType ="Admin";
    }else if(nots.UserType == 2){
      this.userenteredUserType ="Tenant";
    }else if(nots.UserType == 4){
      this.userenteredUserType ="Owner";
    }
    this.userenteredBlockName = nots.BlockName;
    this.userenteredUnitName = nots.UnitName;
    this.userType = nots.UserType;
  }
  getUnits() {
    this.apartmentService.getApartmentBlockUnitByBlockId(parseInt(this.apartmentBlockId)).subscribe((res:any) => {
        this.isBlockSelected = true;
        this.unitData = res;
        if(this.isEditUser){
          this.apartmentBlockUnitId = res[0].apartmentBlockUnitId.toString();
        }
    },
    error => {
      console.log(error);
    }
    );
  }
  dialogClose(){
    this.dialogRef.close();
  }
  submitAddResidentForm(form) {

    this.isUserSubmitted = true;
    this.isError = false;

    if(this.isEditUser) {

      //add user
      let userDetails = {
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "firstName": this.user.firstName,
        "middleName": "",
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "password": this.user.password,
        "salt": "",
        "address1": "",
        "address2": "",
        "city": "",
        "state": "",
        "country": "",
        "zipCode": "",
        "genderId": parseInt(this.genderType),
        "dob": "2019-11-10T09:58:08.934Z",
        "joinedOn": moment().format(),
        "bloodGroup": "",
        "phoneNumber": this.user.phoneNumber.toString(),
        "emergencyContactNumber": "",
        "emergencyContactPerson": "",
        "emergencyContactNumberSecondary": "",
        "emergencyContactPersonSecondary": "",
        "accessCardNumber": "",
        "notes": "",
        "profilePictureId": null,
        "isLiving": true,
        "isActive": true,
        "isApproved": true,
        "resetPassword": true,
        "insertedBy": parseInt(this.cookieService.get('userId')),
        "updatedBy": 0,
        "isDocSubmitted": false,
        "readyForApproval": false
      }

      //add user 
      this.userService.addUser(userDetails).subscribe((res:any) => {
            
            if(res.message) {

              var userId = res.message;

                let userRole = {
                  "userId": userId,
                  "roleId": parseInt(this.userType),
                  "isActive": true,
                  "insertedBy": parseInt(this.cookieService.get('userId')),
                  "insertedOn": moment().format(),
                  "updatedBy": 0,
                  "updatedOn": "2019-11-10T10:00:28.212Z"
                }

                this.userService.addUserRole(userRole).subscribe((res:any) => {

                  var apartmentBlockUnitUser = {
                    "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                    "userId": userId,
                    "isPrimaryContact":true,
                    "userTypeId": parseInt(this.userType),
                    "isActive": true,
                    "insertedBy": parseInt(this.cookieService.get('userId')),
                    "insertedOn": moment().format(),
                    "updatedBy": 0,
                    "updatedOn": "2019-11-10T10:38:46.060Z"
                  }

                  this.apartmentService.addApartmentBlockUnitUser(apartmentBlockUnitUser).subscribe((res:any) => {

                    this.isUserSubmitted = false;

                    if(res.message) {

                      let emailDetails = {
                        emailAddress: this.user.emailId,
                        Name: this.user.firstName,
                        Subject: 'User Created',
                        TextMessage: 'User Created'
                      }

                      this.emailSendService.SendEmail(emailDetails).subscribe((res:any) => {
                      this.sharedService.setAlertMessage("Account has been created. User notified via Email ");
                      this.dialogRef.close();
                      this.router.navigate(['ams/unit&users/usersignuprequest']);
                      },
                      error => {
                        console.log(error);
                      }
                      );

                       
                    }
                    else {
                        this.isError = true;
                        this.errorMessage = res.errorMessage;
                    }

                  },
                  error => {
                    console.log(error);
                  }
                  );

                },
                error => {
                  console.log(error);
                }
                );

            }
            else {

              this.isUserSubmitted = false;
              this.isError = true;
              this.errorMessage = res.errorMessage;

            }

      },
      error => {
        
      }
      );


    }
  
  }

}
