import { Component, OnInit  } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { Location } from '@angular/common';


@Component({
  selector: 'app-mobile-breadcrumb',
  templateUrl: './mobile-breadcrumb.component.html',
  styleUrls: ['./mobile-breadcrumb.component.scss']
})
export class MobileBreadcrumbComponent implements OnInit {
  
  breadcrumbList:any;
  pageName:any;
  pageUrl:any;
 
  constructor(
  	private router: Router,
  	private route: ActivatedRoute,
  	private _location: Location
  ) { 

  	router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
        	
        this.breadcrumbList = this.router.url.split('/');
        this.breadcrumbList.splice(0,2);
        this.pageName = this.breadcrumbList[this.breadcrumbList.length-1];
        this.pageUrl = this.breadcrumbList[0];

      }
    });

  }

  backLink(){
  	this._location.back();
  }

  formatName(name){
  	return name.replace(/-/g, ' ');
  }

  ngOnInit() {
  }

}
