import { Component, OnInit, Inject } from '@angular/core';
import { MatDialog, MatDialogRef, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { LookupService } from 'src/app/api/services/lookup.service';
@Component({
  selector: 'app-add-doc-criteria',
  templateUrl: './add-doc-criteria.component.html',
  styleUrls: ['./add-doc-criteria.component.scss']
})
export class AddDocCriteriaComponent implements OnInit {

  config;
  docForm: FormGroup;
  documentTypeDataList;
  isEdit = false;
  constructor(
    public dialogRef: MatDialogRef<AddDocCriteriaComponent>,
    @Inject(MAT_DIALOG_DATA) public data,
    private lookupService: LookupService
  ) {
    this.config = data;
    console.log(this.config);
    
  }

  ngOnInit() {
    this.getDocumentType();
    this.docForm = new FormGroup({
      name: new FormControl('', [Validators.required]),
      ownerRequired: new FormControl('false'),
      tenantRequired: new FormControl('false'),
      category: new FormControl('', [Validators.required]),
      type: new FormControl('', [Validators.required]),
      description: new FormControl('')
    });
    if (this.config && this.config.type === 'EDIT') {
      this.isEdit = true;
      const r = this.config.data;
      this.docForm.patchValue({
        name: r.description,
        ownerRequired: r.owner,
        tenantRequired: r.tenant,
        type: r.documentTypeId,
        category: r.isForeignNational ? 'Foreign' : 'General',
        description: r.comments
      });
    }
  }

  getValue(control: string) {
    return this.docForm.controls[control].value;
  }

  canShowError(control: string) {
    const field = this.docForm.controls[control];
    return (!field.valid) && (field.touched) && (field.dirty);
  }

  hasError(control: string, error: string) {
    return this.docForm.controls[control].hasError(error);
  }

  close(): void {
    this.dialogRef.close(false);
  }

  getDocumentType() {
    this.lookupService.getLookupValueByLookupTypeId(72).subscribe((res: any) => {

      this.documentTypeDataList = res.filter(item => {
        return item.isActive;
      });
    },
      error => {

      }
    );
  }

  submit() {
    if (this.isEdit) {

    } else {

    }
  }
}
