import { Pipe, PipeTransform } from '@angular/core';

import * as _ from 'underscore';

@Pipe({
  name: 'simpleSearch'
})

export class SimpleSearchPipe implements PipeTransform {

    transform(value: any[], input: string) {
    
    if (!input) {
      return value;
    }
    return value.filter(item => {
      for (let field in item) {
        if (item[field] === null || item[field] === undefined){
          continue;
        }
        if (item[field].toString().toLowerCase().indexOf(input.toLowerCase()) !== -1) {
          return true;
        }
      }
      return false;
    });
    
  }

}

@Pipe({
  name: 'columnSearch',
  pure: false
})
export class ColumnSearchPipe implements PipeTransform {

  transform(data: any, fields: any, selectedInput: any): any {
    if(!data) return;

    return data.filter( row => {
      if(row[selectedInput] !== null && row[selectedInput] && fields[selectedInput]){
        return row[selectedInput].toString().toLowerCase().includes(fields[selectedInput].toLowerCase())
      }
      return true
    })
  }

}


@Pipe( {
name: 'orderBy'
} )

 export class OrderByPipe implements PipeTransform {

 transform(array, orderBy: string, asc: boolean = true){

     if (!orderBy || orderBy.trim() == ""){
       return array;
     } 

     //ascending
     if (asc){
       return Array.from(array).sort((item1: any, item2: any) => { 
         return this.orderByComparator(item1[orderBy], item2[orderBy]);
       });
     }
     else{
       //not asc
       return Array.from(array).sort((item1: any, item2: any) => { 
         return this.orderByComparator(item2[orderBy], item1[orderBy]);
       });
     }

 }

 orderByComparator(a:any, b:any):number{


   if(a === undefined || b === undefined || a == null || b == null) {
     return 0;
   }
   else {

     if((isNaN(parseFloat(a)) || !isFinite(a)) || (isNaN(parseFloat(b)) || !isFinite(b))){
       //Isn't a number so lowercase the string to properly compare
       if(a.toLowerCase() < b.toLowerCase()) return -1;

       if(a.toLowerCase() > b.toLowerCase()) return 1;
     }
     else{
       //Parse strings as numbers to compare properly
       
       if(parseFloat(a) < parseFloat(b)) return -1;
       if(parseFloat(a) > parseFloat(b)) return 1;
      }

     return 0; //equal each other

   }

     
 }
} 

