@Pipe({
  name: 'pager'
})

export class PagerPipe implements PipeTransform {
    
    transform(array: any,  start:any, end:any) {
      return array.slice(start, end);
    }

}

@Pipe({
  name: 'notAvail'
})

export class notAvailPipe implements PipeTransform {

    transform(value: any) {

      return (value != null && value != "") || (value != undefined) ? value : 'NA';
    }

}

@Pipe({
  name: 'isdefined'
})

export class definedObjectPipe implements PipeTransform {

    transform(value: any, obj:any ) {
      if(value === undefined || value.length == 0) {
       return 'NA'
      }
      else {
         var data = value[0];
         return data[obj]
      }
    }

}


@Pipe({
  name: 'notAvailNum'
})

export class notAvailNumPipe implements PipeTransform {

    transform(value: any) {
      return (value == null) ? 'NA' : value;
    }

}



