import { Component, OnInit } from '@angular/core';

import { SharedService } from '../../services/shared.service';


@Component({
  selector: 'app-alert-air',
  templateUrl: './alert-air.component.html',
  styleUrls: ['./alert-air.component.scss']
})
export class AlertAirComponent implements OnInit {

  message:string = "";

  constructor(
  	 private sharedService:SharedService
  ) { }


  isMessageAvailable(){
  	return this.message != "" ? "show" : "";
  }

  closeMessage(){
  	this.message = "";
  }

  ngOnInit() {

  	this.sharedService.alertmessagecast.subscribe( message => {

  		this.message = message;

  		setTimeout(() => {
  			this.message = "";
  		}, 8000);

  	});

  }

}
