import { Component, OnInit, Injector, HostListener } from '@angular/core';

import { UserService } from '../../../api/services/user.service';
import { StaffService } from '../../../api/services/staff.service';
import { TicketService } from '../../../api/services/ticket.service';
import { LookupService } from '../../../api/services/lookup.service';
import { SharedService } from '../../../shared/services/shared.service';
import { ModalService } from '../../../shared/services/modal.service';
import { CookieService } from 'ngx-cookie-service';

import * as _ from 'underscore';


@Component({
  selector: 'app-helpdesk-all-tickets',
  templateUrl: './helpdesk-all-tickets.component.html',
  styleUrls: ['./helpdesk-all-tickets.component.scss']
})
export class HelpdeskAllTicketsComponent implements OnInit {

  ticketData:string = "";
  ticketListData:any;

  isTicketDataLoaded:boolean = false;

  slotFieldType:string = "unitno";
  slotData:boolean = false;

  ItemStartIndex:any = 0;
  ItemEndIndex:any;
  totalItems:any;
  itemLimit:any = 10;

  ticketStatusData:any = {};
  ticketTypeData:any = {};
  ticketPrivateCategoryData:any = {};
  ticketCommonCategoryData:any = {};
  ticketAssignedToData:any = {};
  ticketPriorityData:any = {};

  ticketType:string = "";

  modalService:any;

  ticket:any;

  assignedToData:any;
  raisedByData:any;

  selectedInput:string = "";
  columnField: any = {}

  constructor(
    private injector: Injector,
    private userService:UserService,
    private staffService:StaffService,
    private ticketService: TicketService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService
  ) { 
    this.modalService = this.injector.get(ModalService);
  }


  getTicketStatus(ticket, statusId){

    var data = _.filter( this.ticketStatusData, function( item ) {
      if(item.lookupValueId === statusId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.ticketListData.map(obj=> {
        if(obj.ticketId == ticket.ticketId){
          obj.ticketStatus = data[0].lookupValueName.toLowerCase();
        }
      });

      return data[0].lookupValueName.toLowerCase();
    }

    
  }

  getTicketCategory(categoryId) {
      
    if(this.ticketType == "16"){

      var data = _.filter( this.ticketPrivateCategoryData, function( item ) {
      if(item.lookupValueId === categoryId)
        return item;
      } );

      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    else {

      var data = _.filter( this.ticketCommonCategoryData, function( item ) {
      if(item.lookupValueId === categoryId)
        return item;
      } );

      if(data === undefined || data.length == 0){
        return '';
      }
      else {
        return data[0].lookupValueName;
      }

    }
    

  }


  getTicketType(ticket, typeId) {
    
    var data = _.filter( this.ticketTypeData, function( item ) {
      if(item.lookupValueId === typeId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.ticketListData.map(obj=> {
        if(obj.ticketId == ticket.ticketId){
          obj.ticketType = data[0].lookupValueName;
        }
      });

      return data[0].lookupValueName;
    }

  }

  getTicketAssigned(ticket, assignedId) {

   var data = _.filter( this.assignedToData, function( item ) {
      if(item.staffId === assignedId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {
        
      this.ticketListData.map(obj=> {
        if(obj.ticketId == ticket.ticketId){
          obj.assignedToUser = data[0].firstName;
        }
      });

      return data[0].firstName;
    }

  }

  getTicketRequested(ticket, raisedById) {
     
    var data = _.filter( this.raisedByData, function( item ) {
      if(item.userId === raisedById)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.ticketListData.map(obj=> {
        if(obj.ticketId == ticket.ticketId){
          obj.raisedByUser = data[0].firstName;
        }
      });

      return data[0].firstName;
    }

  }

  getTicketPriority(ticket, priorityId){
    
    var data = _.filter( this.ticketPriorityData, function( item ) {
      if(item.lookupValueId === priorityId)
        return item;
    } );

    if(data === undefined || data.length == 0){
      return '';
    }
    else {

      this.ticketListData.map(obj=> {
        if(obj.ticketId == ticket.ticketId){
          obj.ticketPriority = data[0].lookupValueName.toLowerCase();
        }
      });

      return data[0].lookupValueName.toLowerCase();
    }

  }

  getIndexParams(event){
    this.ItemStartIndex = event.ItemStartIndex;
    this.ItemEndIndex = event.ItemEndIndex;
    this.itemLimit = event.itemLimit;
  }

  sortUnitData(type){
    this.slotFieldType = type;
    this.slotData = !this.slotData;
  }

  getFieldOrderBy(type) {
    if(this.slotFieldType == type){
      return this.slotData ? 'desc' : 'asc';
    }
    else return '';
  }

  selectColInput(value){
    this.selectedInput = value;
  }

  onSelectChange(event, type, name){

    if(!_.isEmpty(event)){
      this.selectedInput = type;
      this.columnField[type] = event[name];
    }
    else {
       this.columnField = {};
    }
    
  }

  isMobileView() {
    return window.innerWidth <= 767 ? 'table-responsive' : '';
  }

  showConfirmModal(id) {
    this.modalService.showConfirmModal(id);
  }

  isItemsAvailable(){
    return this.totalItems > 0 ? true : false;
  }

  isNoItemsAvailable(){
    return this.totalItems == 0 ? true : false;
  }

  isAdmin(){
    return ( this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') ? true : false;
  }

  ngOnInit() {

    if(this.cookieService.get('userRole') == 'Admin' || this.cookieService.get('userRole') == 'Staff') {

        this.ticketService.getAllTicketsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
            
          var ticketListData = res;

          //filter active true items
          this.ticketListData = ticketListData.filter(data => {
            return data.isActive
          })

          console.log(this.ticketListData);

          this.totalItems = this.ticketListData.length;

          if(this.totalItems>this.itemLimit){
            this.ItemEndIndex = this.itemLimit;
          }
          else {
            this.ItemEndIndex = this.totalItems;
          }

          this.isTicketDataLoaded = true;

      },
      error => {
        
      }
      );

    }
    
    if(this.cookieService.get('userRole') == 'Tenant' || this.cookieService.get('userRole') == 'Owner') {

      this.ticketService.getTicketsByRaisedUserId(parseInt(this.cookieService.get('userId'))).subscribe((res:any) => {
            
          var ticketListData = res;

          //filter active true items
          this.ticketListData = ticketListData.filter(data => {
            return data.isActive
          })

          this.totalItems = this.ticketListData.length;

          if(this.totalItems>this.itemLimit){
            this.ItemEndIndex = this.itemLimit;
          }
          else {
            this.ItemEndIndex = this.totalItems;
          }

          this.isTicketDataLoaded = true;

      },
      error => {
        
      }
      );

    }
    

    //get type
    this.lookupService.getLookupValueByLookupTypeId(7).subscribe((res:any) => {

      this.ticketTypeData = res;
    },
    error => {
      
    }
    );

    //get private category
    this.lookupService.getLookupValueByLookupTypeId(16).subscribe((res:any) => {

      this.ticketPrivateCategoryData = res;
    },
    error => {
      
    }
    );

    //get common category
    this.lookupService.getLookupValueByLookupTypeId(17).subscribe((res:any) => {

      this.ticketCommonCategoryData = res;
    },
    error => {
      
    }
    );


    //get status
    this.lookupService.getLookupValueByLookupTypeId(9).subscribe((res:any) => {

      this.ticketStatusData = res;
    },
    error => {
      
    }
    );

    //get priority
    this.lookupService.getLookupValueByLookupTypeId(14).subscribe((res:any) => {

      this.ticketPriorityData = res;
    },
    error => {
      
    });

    //get staffs
    this.staffService.getAllStaffs().subscribe((res:any) => {
      this.assignedToData = res;
    },
    error => {
      
    });

    //get all users
    this.userService.getAllUsersByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {
      this.raisedByData = res;
    },
    error => {
      
    });

        

    // delete item
    this.sharedService.unitlistdeleteindexcast.subscribe( id => {

      if(id != null){

        var params = {
          ticketId: id,
          deleteBy: parseInt(this.cookieService.get('userId'))
        }


        this.ticketService.deleteTicket(params).subscribe((res:any) => {

          _.each(this.ticketListData, (type)=>{
            if(type.ticketId == id){
              type.isActive = false;
            }
          })

          setTimeout(() => {

            this.ticketListData = this.ticketListData.filter((type) => type.ticketId !== id);
            this.totalItems = this.ticketListData.length;

            this.sharedService.setAlertMessage("Ticket deleted");
            
            this.sharedService.setUnitListDeleteIndex(null);

          }, 500)

          

        }, error => {
          console.log(error);
        });

        
      }

    });



  }

}


