import { Component, OnInit, Input, OnChanges, SimpleChanges } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

@Component({
  selector: 'app-nav-pills-tabs',
  templateUrl: './nav-pills-tabs.component.html',
  styleUrls: ['./nav-pills-tabs.component.scss']
})
export class NavPillsTabsComponent implements OnInit {
  
  pageList:any;
  activeNavName:any;

  @Input() navArray:any;

  constructor(
    private router: Router,
    private route: ActivatedRoute
  ) { 

    router.events.subscribe((event: any) => {
      if (event instanceof NavigationEnd) {
          
        this.pageList = this.router.url.split('/');
        this.pageList.splice(0,2);
        this.activeNavName = this.pageList[this.pageList.length-1];

      }
    });

  }

  formatName(name){
    return name.replace(/-/g, ' ');
  }

  ngOnInit() {

  }

  ngOnChanges(changes: SimpleChanges) {

    

  }

}
