import { Injectable } from '@angular/core';
import { BehaviorSubject, Observable } from 'rxjs';
import { Router } from '@angular/router';

import { HttpClient, HttpHeaders, HttpParams } from '@angular/common/http';

import { CookieService } from 'ngx-cookie-service';

@Injectable({
  providedIn: 'root'
})
export class SharedService {


  private sidebarMini = new BehaviorSubject<boolean>(false);
  sidebarminicast = this.sidebarMini.asObservable();

  private unitListDeleteIndex = new BehaviorSubject<any>(null);
  unitlistdeleteindexcast = this.unitListDeleteIndex.asObservable();

  private isApartmenSelected = new BehaviorSubject<boolean>(false);
  apartmentselectedcast = this.isApartmenSelected.asObservable();

  private userDetails = new BehaviorSubject<any>(null);
  userdetailscast = this.userDetails.asObservable();

  private userLogin = new BehaviorSubject<boolean>(false);
  userlogin = this.userLogin.asObservable();

  private alertMessage = new BehaviorSubject<any>("");
  alertmessagecast = this.alertMessage.asObservable();

  private alertErrorMessage = new BehaviorSubject<any>({msg:"",type:""});
  alerterrormessagecast = this.alertErrorMessage.asObservable();

  private glGroupAdded = new BehaviorSubject<any>({id:1, status: false});
  glgroupaddedcast = this.glGroupAdded.asObservable();

  private glGroupDeleteTypeId = new BehaviorSubject<any>(null);
  glgroupdeletetypeidcast = this.glGroupDeleteTypeId.asObservable();

  private glGroupAccount = new BehaviorSubject<boolean>(false);
  glaccountaddedcast = this.glGroupAccount.asObservable();

  private multiInvoiceData = new BehaviorSubject<any>(null);
  multiinvoicedatacast = this.multiInvoiceData.asObservable();

  constructor(
    private router: Router,
    private http: HttpClient,
    private cookieService:CookieService
  ) { }

  getJsonData(): Observable<any>{
    return this.http.get('assets/json/data.json');
  }

  setUnitListDeleteIndex(value) {
    this.unitListDeleteIndex.next(value);
  }

  setSideBarMini(value) {
     this.sidebarMini.next(value);
  }

  setApartmentSelected(value){
    this.isApartmenSelected.next(value);
  }

  setUserDetails(value){
    this.userDetails.next(value);
  }

  isUserLogged(value){
    this.userLogin.next(value);
  }

  setAlertMessage(value){
    this.alertMessage.next(value);
  }

  setGlGroupAdded(data){
    this.glGroupAdded.next(data);
  }

   setGlAccountAdded(data){
    this.glGroupAccount.next(data);
  }

  setGlGroupDeleteTypeId(data){
    this.glGroupDeleteTypeId.next(data);
  }
  

  setMultiInvoiceData(value){
    this.multiInvoiceData.next(value);
  }

  logout() {
    
    this.clearCookies();

    setTimeout(() => {
      this.router.navigate(['home']);
    }, 1000); 
  }

  clearCookies(){

    var cookies = document.cookie.split(";");

    for (var i = 0; i < cookies.length; i++) {
        var cookie = cookies[i];
        var eqPos = cookie.indexOf("=");
        var name = eqPos > -1 ? cookie.substr(0, eqPos) : cookie;
        document.cookie = name + "=;expires=Thu, 01 Jan 1970 00:00:00 GMT";
    }

    //console.log(document.cookie);

    //this.cookieService.deleteAll();
    this.setAlertMessage("");

  }


}
