import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-dash-filter-menu',
  templateUrl: './dash-filter-menu.component.html',
  styleUrls: ['./dash-filter-menu.component.scss']
})
export class DashFilterMenuComponent implements OnInit {
  
  @Input() type:any;

  constructor() { }

  stopPropagation(event: Event){
    event.stopPropagation();
  }

  isTransType(){
    return this.type == 'trans' ? true : false;
  }

  isNormalType(){
    return this.type == 'normal' ? true : false;
  }

  ngOnInit() {
  }

}
