import { Injectable } from '@angular/core';

import { MatDialog } from '@angular/material/dialog';
import { MatSnackBar } from '@angular/material/snack-bar';

import { ConfirmModalComponent } from '.././components/confirm-modal/confirm-modal.component';
import { ErrorModalComponent } from '.././components/error-modal/error-modal.component';
import { ApartmentDialogComponent } from '.././components/apartment-dialog/apartment-dialog.component';
import { UserInfoComponent } from '.././components/user-info/user-info.component';
import { PropertyUnitsDetailsComponent } from 'src/app/ams/my-property/components/property-view/property-units-details/property-units-details.component';
import { FacilityApproveConfirmationModalComponent } from 'src/app/ams/facility/components/facility-approve-confirmation-modal/facility-approve-confirmation-modal.component';
import { FacilityRejectConfirmationModalComponent } from 'src/app/ams/facility/components/facility-reject-confirmation-modal/facility-reject-confirmation-modal.component';
import { FacilityRejectActionComponent } from 'src/app/ams/facility/components/facility-reject-action/facility-reject-action.component';
import { ConfirmCancelComponent } from '../components/confirm-modal/confirm-cancel/confirm-cancel.component';
import { UnapprovedEditModalComponent } from 'src/app/ams/unit-users/components/unapproved-edit-modal/unapproved-edit-modal.component';
import { SignupEditModalComponent } from 'src/app/ams/unit-users/components/signup-edit-modal/signup-edit-modal.component';
import { TicketEditComponent } from '.././components/helpdesk-all-tickets/ticket-edit-model/ticket-edit.component';
import { AlertMessageComponent } from '../../shared/components/alert-message/alert-message.component';
import { ViewMoveinDetailComponent } from '../../shared/components/view-movein-detail/view-movein-detail.component';

@Injectable({
  providedIn: 'root'
})
export class ModalService {

  constructor(
  	public dialog: MatDialog,
    private _snackBar: MatSnackBar,
  ) { }

  showConfirmModal(id){
  	const dialogRef = this.dialog.open(ConfirmModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: id
    });
  }

  showErrorModal(message){

    const dialogRef = this.dialog.open(ErrorModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: message
    });

  }

  showApartmentModal(){
    const dialogRef = this.dialog.open(ApartmentDialogComponent, {
      panelClass: 'material',
      disableClose: true
    });

  }

  showUserInfoModal(user){
    const dialogRef = this.dialog.open(UserInfoComponent, {
      panelClass: 'material',
      disableClose: true,
      data: user
    });
  }

  showUnitDetailsModal(units){
  	const dialogRef = this.dialog.open(PropertyUnitsDetailsComponent, {
      panelClass: 'medium',
      disableClose: false,
      data: units
    });
  }

  showRejectModal(facilityData){
  	const dialogRef = this.dialog.open(FacilityRejectConfirmationModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: facilityData
    });
  }

  showApproveModal(facilityData){
  	const dialogRef = this.dialog.open(FacilityApproveConfirmationModalComponent, {
      panelClass: 'material',
      disableClose: true,
      data: facilityData
    });
  }
  showRejctDetailsModal(facilityData){
  	const dialogRef = this.dialog.open(FacilityRejectActionComponent, {
      panelClass: 'material',
      disableClose: true,
      data: facilityData
    });
  }
  
  showBookingCancelConfirmModal(bookingData){
  	const dialogRef = this.dialog.open(ConfirmCancelComponent, {
      panelClass: 'material',
      disableClose: true,
      data: bookingData
    });
  }
  showUnApprovedetailsModal(unapprovedData){
  	const dialogRef = this.dialog.open(UnapprovedEditModalComponent, {
      panelClass: 'medium',
      disableClose: false,
      data: unapprovedData
    });
  }
  showSignUpdetailsModal(signupData){
  	const dialogRef = this.dialog.open(SignupEditModalComponent, {
      panelClass: 'medium',
      disableClose: false,
      data: signupData
    });
  }
  showTicketEditModal(message){

    const dialogRef = this.dialog.open(TicketEditComponent, {
      panelClass: 'material',
      disableClose: true,
      data: message
    });

  }
  showMoveInDetails(details){

    const dialogRef = this.dialog.open(ViewMoveinDetailComponent, {
      data: {
        data: details
      }
    });

    dialogRef.afterClosed().subscribe(result => {

    });

  }

}
