import { Component, OnInit, Input, ElementRef, ViewChild, Output, EventEmitter } from '@angular/core';

import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'app-simple-date-box',
  templateUrl: './simple-date-box.component.html',
  styleUrls: ['./simple-date-box.component.scss'],
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class SimpleDateBoxComponent implements OnInit {
  
  @Input() dateModel:any;

  @Output() inputChange = new EventEmitter();

  isItemSelected:boolean = false;

  @ViewChild('dateFieldElem', { static: true }) el:ElementRef;

  constructor() { }

  onDateChange(){
  	this.inputChange.emit(this.dateModel);
  	this.isItemSelected = true;
  }

  clearItems(){
  	this.dateModel = "";
    this.inputChange.emit(null);
    this.isItemSelected = false;
  }

  ngOnInit(): void {
  }

}
