import { Component, OnInit, ChangeDetectorRef, Optional, Inject } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { UserService, EmailSendService, ApartmentService, LookupService } from 'src/app/api/services';
import { SharedService } from 'src/app/shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';

@Component({
  selector: 'app-unapproved-edit-modal',
  templateUrl: './unapproved-edit-modal.component.html',
  styleUrls: ['./unapproved-edit-modal.component.scss']
})
export class UnapprovedEditModalComponent implements OnInit {
  blocksData:any;
  unitData:any;
  userRolesData:any;

  userType:string = null;
  genderType:string = null;

  apartmentBlockId:string = "";
  apartmentBlockUnitId:string = "";
  userRoleId:string = "";

  isEditUser:boolean = false;

  isBlockSelected:boolean = false;

  isUserSubmitted:boolean = false;

  isError:boolean = false;
  errorMessage:string = "";

  user:any;

  isValidEmail:boolean = false;
  emailToRegister:string="";
  isAdminRegister:boolean = false;
  isAutoGenPassword:boolean = false;
  constructor(
    private router: Router,
    private route: ActivatedRoute,
    private userService:UserService,
    private emailSendService:EmailSendService,
    private apartmentService:ApartmentService,
    private lookupService: LookupService,
    private sharedService:SharedService,
    private cookieService:CookieService,
    private cd:ChangeDetectorRef,
    @Optional() @Inject(MAT_DIALOG_DATA) public unapprovedData: any,
    private dialogRef: MatDialogRef<UnapprovedEditModalComponent>
  ) { }

  ngOnInit() {
    console.log(this.unapprovedData);
    this.user = this.unapprovedData;
    this.user.isEmailNotify = true;

    //get apartment blocks
    this.apartmentService.getApartmentBlockByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.blocksData = res;
      
    });
  }
  submitAddResidentForm(form) {

    this.isUserSubmitted = true;
    this.isError = false;
      //edit user
      var userDetails = {
        "userId": this.route.params['value'].id,
        "apartmentId": parseInt(this.cookieService.get('apartmentId')),
        "firstName": this.user.firstName,
        "middleName": this.user.middleName,
        "lastName": this.user.lastName,
        "emailId": this.user.emailId,
        "password": this.user.password,
        "salt": this.user.salt,
        "address1": this.user.address1,
        "address2": this.user.address2,
        "city": this.user.city,
        "state": this.user.state,
        "country": this.user.country,
        "zipCode": this.user.zipCode,
        "genderId": parseInt(this.genderType),
        "dob": this.user.dob,
        "joinedOn": this.user.joinedOn,
        "bloodGroup": this.user.bloodGroup,
        "phoneNumber": this.user.phoneNumber.toString(),
        "emergencyContactNumber": this.user.emergencyContactNumber,
        "emergencyContactPerson": this.user.emergencyContactPerson,
        "emergencyContactNumberSecondary": this.user.emergencyContactNumberSecondary,
        "emergencyContactPersonSecondary": this.user.emergencyContactPersonSecondary,
        "accessCardNumber": this.user.accessCardNumber,
        "notes":  this.user.notes,
        "profilePictureId": null,
        "isLiving": true,
        "isActive": true,
        "isApproved": this.isAdminRegister,
        "resetPassword": this.user.resetPassword,
        "insertedBy": this.user.insertedBy,
        "updatedBy": parseInt(this.cookieService.get('userId')),
        "isDocSubmitted": this.user.isDocSubmitted,
        "readyForApproval": this.user.readyForApproval
      }
      //edit user 
      this.userService.UpdateUser(userDetails).subscribe((res:any) => {
            
            if(res.message) {

              var userId = res.message;

                var userRole = {
                  "userId": userId,
                  "roleId": parseInt(this.userType),
                  "isActive": this.user.isActive,
                  "insertedBy": parseInt(this.cookieService.get('userId')),
                  "insertedOn": this.user.insertedOn,
                  "updatedBy": this.user.updatedBy,
                  "updatedOn": this.user.updatedOn
                }

                this.userService.addUserRole(userRole).subscribe((res:any) => {

                  var apartmentBlockUnitUser = {
                    "apartmentBlockUnitId": parseInt(this.apartmentBlockUnitId),
                    "userId": userId,
                    "isPrimaryContact": true,
                    "userTypeId": parseInt(this.userType),
                    "isActive": this.user.isActive,
                    "insertedBy": parseInt(this.cookieService.get('userId')),
                    "insertedOn": this.user.insertedOn,
                    "updatedBy": this.user.updatedBy,
                    "updatedOn": this.user.updatedOn
                  }

                  this.apartmentService.addApartmentBlockUnitUser(apartmentBlockUnitUser).subscribe((res:any) => {

                    this.isUserSubmitted = false;

                    if(res.message) {
                       this.sharedService.setAlertMessage("User updated successfully");
                       this.router.navigate(['ams/unit&users/unapproved']);
                    }
                    else {
                        this.isError = true;
                        this.errorMessage = res.errorMessage;
                    }

                  },
                  error => {
                    console.log(error);
                  }
                  );

                },
                error => {
                  console.log(error);
                }
                );

            }
            else {
              this.isUserSubmitted = false;
              this.isError = true;
              this.errorMessage = res.errorMessage;
            }

      },
      error => {
        
      }
      );
  }

  getUnits() {
    this.apartmentService.getApartmentBlockUnitByBlockId(parseInt(this.apartmentBlockId)).subscribe((res:any) => {
        this.isBlockSelected = true;
        this.unitData = res;
        if(this.isEditUser){
          this.apartmentBlockUnitId = res[0].apartmentBlockUnitId.toString();
        }
    },
    error => {
      console.log(error);
    }
    );
  }
  dialogClose(){
    this.dialogRef.close();
  }
}
