import { Component, OnInit } from '@angular/core';
import { Router, NavigationStart, NavigationEnd, ActivatedRoute } from '@angular/router';

import { FacilityService } from '../../../api/services/facility.service';
import { LookupService } from '../../../api/services/lookup.service';
import { SharedService } from '../../../shared/services/shared.service';
import { CookieService } from 'ngx-cookie-service';


import dayGridPlugin from '@fullcalendar/daygrid';

import * as moment from 'moment';
import * as _ from 'underscore';


@Component({
  selector: 'app-full-calendar',
  templateUrl: './full-calendar.component.html',
  styleUrls: ['./full-calendar.component.scss']
})
export class FullCalendarComponent implements OnInit {
  
  calendarPlugins = [dayGridPlugin]; // important!

  calendarEvents:any = [];

  facilityBookingData:any;

  facilityCategoryData:any;
  facilitySlotData:any;

  isCalenderLoaded:boolean = false;

  constructor(
  	private router: Router,
    private route: ActivatedRoute,
	private facilityService:FacilityService,
	private lookupService: LookupService,
	private sharedService:SharedService,
	private cookieService:CookieService
  ) { 

  	this.router.events.subscribe(
	  (event: any) => {
	    if (event instanceof NavigationEnd) {
	      let path = this.router.url;
	     
			if(path.indexOf('facility') != -1){

				this.getFacilityBookingData();

			}
			else {


			}

	    }
	   

	  });

  }

  handleEventClick(arg){
  	
  	var id = arg.event.id;
  	this.router.navigate(['/ams/facility/edit-booking', id]);

  }

  getFacilityBookingData() {

  	this.facilityService.getApartmentFacilitySlotsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

      this.facilitySlotData = res.filter(item => {
        return item.isActive;
      })


    },
    error => {

    });

  	this.facilityService.getApartmentFacilitiesByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {


		this.facilityCategoryData = res.filter(item => {
			return item.isActive;
		})


		this.facilityService.getApartmentFacilityBookingsByApartmentId(parseInt(this.cookieService.get('apartmentId'))).subscribe((res:any) => {

			this.facilityBookingData = res.filter(item => {
				return item.isActive;
			});


			_.each(this.facilityBookingData, booking => {

				var date = moment(booking.bookedForDate).format("YYYY-MM-DD");

				var nameItem = this.facilityCategoryData.filter( item => {
  					return item.apartmentFacilityId == booking.apartmentFacilityId;
  				});

  				var slotData = this.facilitySlotData.filter( item => {
  					return item.apartmentFacilitySlotId == booking.slotId;
  				});

				var beginTime = moment(slotData[0].slotBeginTime, 'HH:mm:ss').toDate();
				var slotBeginTime = moment(beginTime).format('HH:mm A');

				var EndTime = moment(slotData[0].slotEndTime, 'HH:mm:ss').toDate();
				var slotEndTime = moment(EndTime).format('HH:mm A');

  				var event = nameItem[0].facilityName + " ( " + slotBeginTime  + "-" + slotEndTime +" ) ";

				this.calendarEvents.push({ title: event, date: date, id: nameItem[0].apartmentFacilityId });


				this.isCalenderLoaded = true;

			});

		},
		error => {

		});
			      

    },
    error => {

    }); 

  }


  ngOnInit() {


  }

}
