import { Component, OnInit, Input, DoCheck, Output, EventEmitter, ViewChild, ElementRef, HostListener, Renderer2 } from '@angular/core';

import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'app-simple-select-box',
  templateUrl: './simple-select-box.component.html',
  styleUrls: ['./simple-select-box.component.scss'],
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class SimpleSelectBoxComponent implements OnInit {
  
  @Input() fieldName:string;
  @Input() fieldText:string;
  @Input() fieldPlaceholder:string;
  @Input() fieldModel:any;
  @Input() fieldRequired:string;
  @Input() fieldList:any;
  @Input() fieldClass?:any;
  @Input() isDisabled?:boolean;
  @Input() fieldItemName:any;
  @Input() isLabel:any;

  @Output() inputChange = new EventEmitter();

  @Output() inputStatus = new EventEmitter();

  @ViewChild('selectFieldElem', { static: true }) el:ElementRef;

  isItemSelected:boolean = false;

  isExpanded:boolean = false;

  constructor(
    private element: ElementRef, private renderer: Renderer2
  ) { }

  getItemName(item, type){
  	return item[type]
  }

  isFieldRequired(){
    return this.fieldRequired == "required" ? true : false;
  }

  isSelectedItem(item, modal) {
    return item == modal ? 'selected': '';
  }
  
  getSelectedItem(item, type){
  	this.fieldModel = item[type];
  	this.inputChange.emit(item);
    this.isItemSelected = true;
  	this.isExpanded = false;

  }

  clearItems(item){
    this.fieldModel = "";
    this.inputChange.emit(null);
    this.isItemSelected = false;
    this.isExpanded = false;
  }

  @HostListener('document:click', ['$event']) 
    onDocumentClick(event) {

      var el = this.element.nativeElement;

      if(el.children.length > 0){
        
        //console.log(el.children[0]);

        var arrowElement = el.children[0].children[0].children[1].nextSibling;

        const arrowClass = arrowElement.classList.contains('up');

        if (!el.contains(event.target)) {

          if(arrowClass) {
            this.enableSelect();
          }
         
        }

      } 

   }

  enableSelect() {

     var el = this.element.nativeElement.children[0].children[0].children[1]; //input field
      var arrowElement = el.nextSibling;
      var selectDropdown = arrowElement.nextSibling;

      const arrowClass = arrowElement.classList.contains('up');
      const selectClass = selectDropdown.classList.contains('active');

      if(arrowClass) {
        this.renderer.removeClass(arrowElement,"up");
      }
      else {
        this.renderer.addClass(arrowElement,"up");
      }
       
      if(selectClass) {
        this.renderer.removeClass(selectDropdown,"active");
      }
      else {
        this.renderer.addClass(selectDropdown,"active");
      }


   }


  ngOnInit(): void {
  }

  ngAfterViewInit(){

    setTimeout(() => {

      if(this.el.nativeElement.classList.contains('ng-valid')){
        this.inputStatus.emit(true);
      }

    }, 200);

  }

  ngDoCheck() {
    if(this.fieldModel == "null" || this.fieldModel == "undefined" || this.fieldModel == 0){
      this.fieldModel = null;
    }
    
  }

}
