
import { Directive, ElementRef, HostListener, Renderer2, OnInit, EventEmitter, Output } from '@angular/core';
import { NgModel, NgForm, ControlContainer, NgControl } from "@angular/forms";

declare var $:JQueryStatic;

@Directive({
  selector: '.common'
})

export class CommonDirective {
  constructor() {}

}


@Directive({
  selector: '.svg'
})

export class SvgDirective {


  constructor(private element: ElementRef, private renderer: Renderer2) { 

    var elem = this.element.nativeElement;
    var elemClass = elem.classList.value;

    setTimeout(()=>{    
          
      $(elem).each(function() {
        var $img = jQuery(this);
        var imgURL = $img.attr('src');
        var attributes = $img.prop("attributes");

        $.get(imgURL, function(data) {
            // Get the SVG tag, ignore the rest
            var $svg = jQuery(data).find('svg');

            // Remove any invalid XML tags
            $svg = $svg.removeAttr('xmlns:a');

            // Loop through IMG attributes and apply on SVG
            $.each(attributes, function() {
                $svg.attr(this.name, this.value);
            });

            // Replace IMG with SVG
            $img.replaceWith($svg);
        }, 'xml');
      });


   }, 0);

    

    
  }

}


@Directive({
  selector: '.table-resizable',
  providers : [NgModel]
})

export class tableResizableDirective implements OnInit {

  constructor(
    private element: ElementRef, 
    private renderer: Renderer2,
    private ngModel : NgModel
    ) { 


    $(function () {
      
      var startX,
        startWidth,
        $handle,
        $table,
        pressed = false;

      $(document)
        .on({
          mousemove: function (event) {
            if (pressed) {
              $handle.width(startWidth + (event.pageX - startX));
            }
          },
          mouseup: function () {
            if (pressed) {
              $table.removeClass("resizing");
              pressed = false;
            }
          }
        })
        .on("mousedown", ".table-resizable th", function (event) {
          $handle = $(this);
          pressed = true;
          startX = event.pageX;
          startWidth = $handle.width();

          $table = $handle.closest(".table-resizable").addClass("resizing");
        })
        .on("dblclick", ".table-resizable thead", function () {
          // Reset column sizes on double click
          $(this).find("th[style]").css("width", "");
        });
    });
    
  }

   ngOnInit() {
    
   }

}

@Directive({
  selector: '.search',
  providers : [NgModel]
})

export class SearchDirective {

  constructor(
    private element: ElementRef, 
    private renderer: Renderer2,
    private ngModel : NgModel
    ) { 

   
    
  }
  
  @HostListener("click", ["$event"])
   onClick(event) {
     
      this.enableSearch();


   }

   enableSearch() {

     var el = this.element.nativeElement;
      var arrowElement = el.nextSibling;

      if(arrowElement != undefined) {
        var selectDropdown = arrowElement.nextSibling;

        const arrowClass = arrowElement.classList.contains('up');
        const selectClass = selectDropdown.classList.contains('active');

        if(arrowClass) {
          this.renderer.removeClass(arrowElement,"up");
        }
        else {
          this.renderer.addClass(arrowElement,"up");
        }
         
        if(selectClass) {
          this.renderer.removeClass(selectDropdown,"active");
        }
        else {
          this.renderer.addClass(selectDropdown,"active");
        }
      }

   }


   @HostListener('document:click', ['$event']) 
    onDocumentClick(event) {

      var el = this.element.nativeElement;
      var arrowElement = el.nextSibling;

      if(arrowElement != undefined) {
        const arrowClass = arrowElement.classList.contains('up');

        if (!el.contains(event.target)) {

          if(arrowClass) {
            this.enableSearch();
          }
         
        }
      }
     
   }

   

}


@Directive({
  selector: '[OnlyNumber]'
})
export class OnlyNumberDirective {

  // Allow decimal numbers. The \. is only allowed once to occur
  private regex: RegExp = new RegExp(/^[0-9]+(\.[0-9]*){0,1}$/g);

  // Allow key codes for special events. Reflect :
  // Backspace, tab, end, home
  private specialKeys: Array<string> = ['Backspace', 'Tab', 'End', 'Home'];

  constructor(private el: ElementRef) {
  }

  @HostListener('keydown', ['$event'])
  onKeyDown(event: KeyboardEvent) {
    // Allow Backspace, tab, end, and home keys
    if (this.specialKeys.indexOf(event.key) !== -1) {
      return;
    }

    // Do not use event.keycode this is deprecated.
    // See: https://developer.mozilla.org/en-US/docs/Web/API/KeyboardEvent/keyCode
    let current: string = this.el.nativeElement.value;
    // We need this because the current value on the DOM element
    // is not yet updated with the value from this event
    let next: string = current.concat(event.key);
    if (next && !String(next).match(this.regex)) {
      event.preventDefault();
    }
  }
}


@Directive({
  selector: 'input[nullValue]'
})

export class StringDefaultValueDirective {
  constructor(private el: ElementRef, private control: NgControl) {}

  @HostListener('input', ['$event.target'])
  onEvent(target: HTMLInputElement){
    this.control.viewToModelUpdate((target.value == null || target.value == undefined) ? "" : target.value);
  }
}



