import { Component, OnInit, Input, Output, EventEmitter, ViewChild, OnChanges, SimpleChanges, ElementRef, ChangeDetectionStrategy, ChangeDetectorRef} from '@angular/core';

import { ControlContainer, NgForm } from '@angular/forms';

@Component({
  selector: 'app-select-search-box',
  templateUrl: './select-search-box.component.html',
  styleUrls: ['./select-search-box.component.scss'],
  viewProviders: [ { provide: ControlContainer, useExisting: NgForm } ]
})
export class SelectSearchBoxComponent implements OnInit {
  
  @Input() fieldName:string;
  @Input() fieldText:string;
  @Input() fieldPlaceholder:string;
  @Input() fieldModel:any;
  @Input() fieldRequired:string;
  @Input() fieldList:any;
  @Input() isLabel:any;
  @Input() fieldItemName:any;

  selectedItem:any;
  isItemSelected:boolean = false;

  @Output() inputChange = new EventEmitter();

  @Output() inputStatus = new EventEmitter();

  @ViewChild('searchFieldElem', { static: true }) el:ElementRef;

  constructor(
    private cdRef:ChangeDetectorRef
  ) { }

  isFieldRequired(){
    return this.fieldRequired == "required" ? true : false;
  }

  getSelectedItem(item, type) {
    this.fieldModel = item[type];
    this.inputChange.emit(item);
    this.isItemSelected = true;
  }

  getItemName(item, type){
  	return item[type]
  }

  isSelectedItem(item, modal) {
    return item == modal ? 'selected': '';
  }

  clearList() {
    this.fieldModel = "";
    this.isItemSelected = false;
    document.querySelectorAll('.user-search-field-list').forEach(function(elem) {
      elem.classList.remove('selected');
    });
    this.inputChange.emit(null);
  }

  isFieldModelChanged(){
    this.isItemSelected = false;

    setTimeout(() => {

      if(this.el.nativeElement.classList.contains('ng-valid')){
        this.inputStatus.emit(true);
      }
      else {
        this.inputStatus.emit(false);
      }

    }, 200);

    
  }

  ngOnInit() {



  }

  ngAfterViewInit(){

    if(this.el.nativeElement.classList.contains('ng-valid')){
        this.inputStatus.emit(true);
    }

  }

  ngOnChanges(changes: SimpleChanges) {

    if(this.fieldModel != null) {

      if(this.fieldModel != ""){
        this.isItemSelected = true;
      }
      else{
        this.isItemSelected = false;
      }

    }

    


  }

}
